import Highway from '@dogstudio/highway';
import CustomTransition from './tans';

const H = new Highway.Core({
  transitions: {
    default: CustomTransition
  }
});

// Get all menu links
const links = document.querySelectorAll('nav li a');

// Listen the `NAVIGATE_IN` event
// This event is sent everytime a `data-router-view` is added to the DOM Tree
H.on('NAVIGATE_IN', ({ to, location }) => {
  // Check Active Link
  for (let i = 0; i < links.length; i++) {
    const link = links[i];

    // Clean class
    link.classList.remove('is-active');

    // Active link
    if (link.href === location.href) {
      link.classList.add('is-active');
    }
  }

  manageScripts(to);

});

function manageScripts(to) {
  // Your main JS file, used to prepend other scripts
  const main = document.querySelector('#main-script');
  const a = [...to.page.querySelectorAll('script:not([data-no-reload])')];

  for (let j = 0; j < a.length; j++) {
    const d = a[j];

    // Create Shadow Script
    var fileref = document.createElement('script')

    // Loop Over Attributes
    for (let k = 0; k < d.attributes.length; k++) {
      // Get Attribute
      const attr = d.attributes[k];

      // Set Attribute
      fileref.setAttribute(attr.nodeName, attr.nodeValue);
    }
    document.body.insertBefore(fileref, main);
  }

}

